.Input {
    width: 550px;
    margin: 0 auto;
    text-align: center;
    position: relative;
    height: auto;
    padding-bottom: 50px;
    border-bottom: 1px solid gray;
}


.input-submit {
    position: absolute;
    top: 0px;
    bottom: 0;
    right: 0;
    margin: 0 auto;
    width: 45px;
    height: 45px;
    background-color: green;
    mask: url("../../img/play_circle.svg") no-repeat 50% 50% ;
    mask-size: cover;
    cursor: pointer;
}

.input-text {
    width: 100%;
    height: 45px;
    margin: 0 auto;
    padding-left: 20px;
    font-size: 20px;
    border-radius: 30px;
    border: 1px solid gray;
    outline: none;
    /*position: relative;*/
}