.ItemList {
    width: 550px;
    height: 70px;
    border-bottom: 1px solid gray;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 10px;
    transition: all 0.3s linear;
    font-size: 14px;
}

.item-paused {
    background-color: rgba(255, 255, 204, 0.3);
    color: rgba(38, 166, 91, 1);
}



.btn {
    width: 40px;
    height: 40px;
    /*background-color: transparent;*/
    border: none;

}

.pause-btn {
    width: 45px;
    height: 45px;
    background-color: black;
    mask: url("../../img/pause-icon.svg") no-repeat 50% 50% ;
    mask-size: cover;
    cursor: pointer;
    transition: all 0.3s linear;
}


.paused {
    mask: url("../../img/play-icon.svg") no-repeat 50% 50% ;
    mask-size: cover;
}

.delete-btn {
    width: 45px;
    height: 45px;
    background-color: orangered;
    mask: url("../../img/remove-icon.svg") no-repeat 50% 50% ;
    mask-size: cover;
    cursor: pointer;
    transition: all 0.3s linear;
}